body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
#main{
    background: #1c272b;
    height: 100vh;
}
#main .header{
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid gray;
    padding: 0 3rem;
    height: 10%;
}
#main .header h2{
    color: white;
}
#main .header h1{
    color: white;
    font-size: 2rem;
    text-transform: uppercase;
    letter-spacing: 4px;
    border-bottom: 1.5px solid #ffc107;

}
#main .header h1 span{
    text-shadow: 0 0 5px rgb(0 0 0);
}
#main .header .right a{
    position: relative;
    display: inline-block;
    padding: 5px 16px;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    white-space: nowrap;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border-radius: 3px;
    -webkit-appearance: none;
    appearance: none;
    background-color: #58a6ff;
    color: white;
    text-decoration: none;
    margin-right: 1rem;
    transition: all 0.3s ease-in-out;
}
#main .header .right a:hover {
    opacity: 0.93;
}
#main .maincomponent{
    display: flex;
    height: 85vh;
}
#main .maincomponent .left{
    flex: 0.40 1;
    border-right: 1px solid gray;
    overflow-y: scroll;
}
::-webkit-scrollbar{
    width: 5px;
  }
  ::-webkit-scrollbar-track{
    border: 7px solid #232943;
    box-shadow: inset 0 0 2.5px 2px rgba(0,0,0,0.5);
  }
  ::-webkit-scrollbar-thumb{
    background: linear-gradient(
      45deg,
      #0074A8,
      #fff
    );
  }
#main .maincomponent .left .navbar{
    display: flex;
    height: 7%;
    background: #545b62;
    flex-wrap: nowrap;
}
#main .maincomponent .left .navbar li{
    color: white;
    list-style: none;
    width: 20%;
    text-align: center;
    padding: 10px 0;
    font-weight: bold;
    cursor: pointer;
}

#main .maincomponent .left .navbar li:nth-child(1){
    width: 15%;
}
#main .maincomponent .left .navbar .active{
    color: black;
    border-bottom: 2.5px solid #ffc107;
}
#main .maincomponent .right{
    flex: 0.60 1;
}
#main .maincomponent .left .form{
    color: white;
}
.form {
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.form .heading{
    float: left;
    font-size: 30px;
    font-weight: 300;
    padding-left: 1rem;
    position: relative;
}
.form .heading::after{
    content: "";
    position: absolute;
    width: 10%;
    height: 3px;
    background-color: #58a6ff;
    left: 3.2%;
    top: 100%;
}
.form .input-box{
    display: flex;
    flex-wrap: wrap;
    margin: 6px 0;
    align-items: center;
}
.input-box1 {
    display: flex;
    margin: 6px 0;
    align-items: center;
}
.form .input-box span{
    width: 30%;
}
.form .input-box textarea{
    background: none;
    border: none;
    width: 100%;
    height: 7rem;
    color: white;
    font-weight: 300;
    border: 1px solid #58a6ff;
    outline: none;
    font-size: 17px;
    padding: 7px 10px;
    font-family: inherit;
    font-weight: 300;
}
.form .textarea{
    align-items:normal;
    margin-top: 10px;
}
.form .textarea span{
    margin-top: 1rem;
}
.form .input-box input{
    background: none;
    border: none;
    width: 100%;
    height: 2rem;
    color: white;
    font-weight: 300;
    border-bottom: 1px solid #58a6ff;
    outline: none;
    font-size: 17px;
}

.form .input-box1 input{
    background: none;
    border: none;
    width: 100%;
    height: 2rem;
    color: white;
    font-weight: 300;
    border-bottom: 1px solid #58a6ff;
    outline: none;
    font-size: 17px;
}
.skills-section .skill{
    display: inline;
    background: hsl(153deg 63% 42%);
    padding: 2px 25px 2px 11px;
    border-radius: 4px;
    margin: 5px;
