    display: inline-block;
    font-size: 1.2rem;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    flex-shrink: 0;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
