    top: 3px;
    cursor: pointer;
}
.skills-section div{
    display: inline-block;
    margin-bottom: 1rem;
}
.skills-section{
    height: 12rem;
    padding: 1rem .5rem;
    border: 1px solid gray;
    border-radius: 5px;
    margin-top: 1rem;
    box-shadow: inset 0 0 8px 7px rgb(0 0 0 / 20%);
    overflow-y: scroll;
}
.input-box .MuiIconButton-root {
    flex: 0 0 auto;
    color: rgba(0, 0, 0, 0.54);
    padding: 0px 7px;
    margin-left: 1rem;
    overflow: visible;
    font-size: 1.5rem;
    text-align: center;
    transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 50%;
    color: white;
    border: 1px solid #58a6ff;
}
.form > h2{
    float: left;
    font-size: 25px;
    font-weight: 300;
    padding-left: 0rem;
    position: relative;
}
.form > h2::before{
    content: "";
    position: absolute;
    width: 10%;
    height: 1px;
    background-color: #58a6ff;
    left: 0.2%;
