}
.experience{
    border: 1px solid gray;
    margin-top: 1rem;
    border-radius: 3px;
}
.workexp .addexp{
    padding: 0px 9px;
    font-size: 2rem;
    margin-left: 29rem;
    margin-top: 1rem;
    color: white;
    border-radius: 50%;
