    display: flex;
    align-items: inherit;
    justify-content: inherit;
}
.form .achivelist{
    padding: .5rem;
    border-radius: 5px;
    border: 1px solid #58a6ff;
    margin-bottom: 1rem;
    width: 100%;
    overflow: hidden;
    word-wrap: break-word;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.form .achivelist p{
    flex: 0.97 1;
    word-wrap: break-word;
    overflow: hidden;
}
.form .achivelist .MuiSvgIcon-root{
    float: right;
    cursor: pointer;
}
#main .maincomponent .right {
    flex: 0.60 1;
    height: 85vh;
    overflow-y: scroll;
    padding: 1rem;
}
#main .maincomponent .right .resume-preview{
    width: 100%;
    background: white;
}

.fontpicker-div{
    margin-top:5px;
}
.fontpicker-label{
    margin-bottom: 5px;
}
.fontpicker{
    width:235px;
    height:20px;
} 
.fontpicker:focus{ 
   background-color: inherit;
}

.print-button {
    -webkit-appearance: button;
            appearance: button;
    background-color: #000;
    background-image: none;
    border: 1px solid #000;
    border-radius: 4px;
    box-shadow: #fff 4px 4px 0 0,#000 4px 4px 0 1px;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-family: ITCAvantGardeStd-Bk,Arial,sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    margin: 0 5px 10px 0;
    overflow: visible;
    padding: 12px 40px;
    text-align: center;
    text-transform: none;
    touch-action: manipulation;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-select: none;
    vertical-align: middle;
    white-space: nowrap;
  }
  
  .print-button:focus {
    text-decoration: none;
  }
  
  .print-button:hover {
    text-decoration: none;
  }
  
  .print-button:active {
    box-shadow: rgba(0, 0, 0, .125) 0 3px 5px inset;
    outline: 0;
  }
  
  .print-button:not([disabled]):active {
    box-shadow: #fff 2px 2px 0 0, #000 2px 2px 0 1px;
    -webkit-transform: translate(2px, 2px);
            transform: translate(2px, 2px);
  }
  
  @media (min-width: 768px) {
    .print-button {
      padding: 12px 50px;
    }
  }

  .pick-theme-button {
    font-family: "Open Sans", sans-serif;
    font-size: 16px;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    color: #000;
    cursor: pointer;
    border: 3px solid;
    padding: 0.25em 0.5em;
    box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
    position: relative;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
  }
  
  .pick-theme-button:active {
    box-shadow: 0px 0px 0px 0px;
    top: 5px;
    left: 5px;
  }
  
  @media (min-width: 768px) {
    .pick-theme-button {
      padding: 0.25em 0.75em;
    }
  }

